<template>
  <div id="upload">
    <label for="chooseFile" class="item">选择文件{{fileName}}</label>
    <input class="upload" id="chooseFile" type="file" @change="getFile">
    <div class="item" @click="upload">上传文件</div>
    <div class="item" v-if="url">{{url}}</div>
    <div class="item clipbtn" @click="copyMy" v-if="url" :data-clipboard-text="copyText">点我复制</div>
  </div>
</template>
<script>
import Clipboard from 'clipboard'
export default {
  data () {
    return {
      file: null,
      imgFile: '',
      fileName: '',
      url: '',
      copyText: ''
    }
  },
  methods: {
    getFile (e) {
      let target = e.srcElement ? e.srcElement : e.target
      this.file = target.files[0]
      this.fileName = this.file.name
    },
    upload () {
      if (!this.file) {
        return
      }
      // Indicator.open('上传中')
      this.$utils.uploader(this.$utils.cache('user_token'), 'vsn1_1', this.file, this.file.name.split('.')[0]).then(res => {
        this.imgFile = res.url
        this.url = res.url
      })
    },
    copyMy () {
      this.copyText = this.url
      if (Clipboard.isSupported()) {
        let clb = new Clipboard('.clipbtn')
        clb.on('success',  (e) => {
          this.$toast({
            message: '复制成功,请发送给好友',
            position: 'middle',
            duration: 1500,
            iconClass: 'icon icon-success'
          })
          clb.destroy()
        })
        clb.on('error',  (e) => {
          this.$toast({
            message: '复制链接失败了',
            position: 'middle',
            duration: 1500,
            iconClass: 'icon icon-success'
          })
          clb.destroy()
        })
      }
    }
  }
}
</script>
<style lang="scss" scoped>
#chooseFile {
  display: none;
}
.item {
  display: flex;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid#eee;
  display: flex;
  align-items: center;
  padding: 0 30px;
}
</style>



